import { Component, h, State, Prop } from '@stencil/core';
import { LimeWebComponentPlatform } from '@limetech/lime-web-components';

@Component({
    tag: 'layout-login',
    shadow: true,
    styleUrl: 'layout-login.scss',
})
export class LayoutLogin {
    @Prop() public platform: LimeWebComponentPlatform;
    @State() isSignup: boolean = false;

    constructor() {
        this.toggleSignupContainer = this.toggleSignupContainer.bind(this);
    }

    public render() {
        const loginClass = this.isSignup
            ? 'login-container'
            : 'login-container active';
        const signupClass = this.isSignup
            ? 'signup-container active'
            : 'signup-container';

        return [
            <div class="auth-container">
                <div
                    class={loginClass}
                    onClick={() => this.toggleSignupContainer(false)}
                >
                    <h3>Welcome Back</h3>
                    <ga-login platform={this.platform} />
                </div>

                <div
                    class={signupClass}
                    onClick={() => this.toggleSignupContainer(true)}
                >
                    <h3>Create Account</h3>
                    {(() => {
                        if (this.isSignup) {
                            return <ga-signup platform={this.platform} />;
                        } else {
                            return (
                                <limel-input-field
                                    label="Email address"
                                    type="email"
                                    value=""
                                    trailingIcon="filled_message"
                                />
                            );
                        }
                    })()}
                </div>
            </div>,
        ];
    }

    private toggleSignupContainer(value: boolean) {
        this.isSignup = value;
    }
}
